.main {
    position: relative;
    background: url('../img/main-bg.jpg') no-repeat center/cover;
    &:after {
        @include pos-w;
        background-color: rgba(0,0,0, .6);
    }
    &__slider {
        text-transform: uppercase;
        max-width: 700px;
        margin-left: 0;
        padding-top: 18.5%;
        padding-bottom: 18.5%;
        .section__title-text {
            color: $greydark;
        }
        h5 {
            font-size: 1.125rem;
            line-height: 100%;
            color: $greydark;
            font-weight: 500;
            text-align: left;
            margin-bottom: 0;
            padding-left: 60px;
        }
        .slider__title {
            @include adaptFont(30, 64);
            line-height: 100%;
            color: $greydark;
            font-weight: 800;
            letter-spacing: 1px;
            margin-bottom: 15px;
        }
        h4 {
            padding: 10px 0;
            border-radius: 4px;
            background-color: #333333;
            @include adaptFont(14, 17);
            line-height: 130%;
            color: #ffffff;
            font-weight: 500;
            margin-bottom: 45px;
            text-align: center;
            &::first-letter {
                color: $mainClr;
            }
            span {
                font-weight: 800;
                text-transform: initial;
                
            }
        }
        .scroll__arrow {
            margin-top: 45px;
            @include flex-c-c;
            margin: 0 auto;
            transition: transform .3s linear;
            svg {
                width: 55px;
                height: 55px;
                fill: $mainClr;
                @media (max-width: 768px) {
                    width: 45px;
                    height: 45px;
                }
                @media (max-width: 768px) {
                    width: 28px;
                    height: 28px;
                }
            }
            &:hover svg { 
                transform: translateY(5px);
            }
            
        }

    }
}

